export const pi = Math.PI; // es6 

export function power(x, y) {
  return x ** y; // es7  
}

// es6 
export class Foo {
  #private = 10; // stage 3:    

  foo() {
    // stage 4:  Rest/Spread  
    const { a, b, ...x } = { ...{ a: 1, b: 2 }, c: 3, d: 4 };
    return { a, b, x };
  }

  bar() {
    return this.#private;
  }
}
